<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process262"></a>

<p><br></p>
<p><span class="rvts17">寻找阿胡大人（</span><span class="rvts50">Finding Lord Arhu</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:470,Y:632</span><span class="rvts21">，地点：下水道中的一处隐秘房间</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">找到阿虎大人</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1437.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在支线</span><span class="rvts20"></span><span class="rvts21">放假了</span><span class="rvts20"></span><span class="rvts21">中，通过与学校中的小朋友崔娜（</span><span class="rvts20">Trina</span><span class="rvts21">）谈话，并通过说服桥段，她会告诉你地下道中藏着一个隐秘的房间，里面藏了一些不为人知的秘密。</span>
<br><span class="rvts21">进入地下道后，其实，只要有足够高的智力（</span><span class="rvts20">Wits</span><span class="rvts21">），也可以发现这个隐秘的房间入口。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1438.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">遇到一个小女孩叫做大托马拉（</span><span class="rvts20">Big Tomorrah</span><span class="rvts21">）和她的三头小巨魔。与她交谈会有一个劝说桥段，如果通过劝说桥段，那么他们将不会攻击你。但是如果你想要买到她身后那张独特的屏风画，需要花费</span><span class="rvts20">10000</span><span class="rvts21">金。</span>
<br><span class="rvts21">如果未成功通过该桥段，那么大托马拉会命令三头巨魔攻击我们。注意，这</span><span class="rvts20">3</span><span class="rvts21">只巨魔在元素抗性方面都会有自己的强项和弱项，尽可能观察他们的角色属性，并使用他们抗性弱的法术，击败它们！</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1439.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">击败巨魔，便可以得到这张特殊的屏风画。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1440.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">来到林德</span><span class="rvts20"></span><span class="rvts21">凯姆藏宝室，在此处使用屏风与其它的屏风对齐，便可以揭示出一条隐藏的小道。这条小道通往阿虎大人被抓的地下室。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1441.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">使用桌子旁的神王之书，可以打开地下室大门。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1442.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">记得带上这幅叫做《责任》的油画，之后需要用到。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1443.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">来到地下室，再次遇到解密。其实该谜题比较简单，就是把上层的那幅《责任》油画，放到雕像处的桌子上。之后，机关被激活，可以继续往下面走。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1444.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">经证实，阿虎大人被凯姆关进到了这里。使用</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">，可以看到他被周围的三个黑环灵魂所折磨。因此，到达这里后，便可以很好的证明凯姆投靠了黑环。使用</span><span class="rvts20"></span><span class="rvts21">源力汲取</span><span class="rvts20"></span><span class="rvts21">，可以吸走困扰阿虎的三个灵魂，但此时也会瞬间引来黑环士兵的袭击。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1445.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在战斗进入到第一轮的结尾，林德</span><span class="rvts20"> </span><span class="rvts21">凯姆也到达现场。既然事情已经到了撕破脸皮的地步，一场战斗不可避免。该场战斗，林德凯姆是一个相当厉害的圣骑士，攻高防高，特别耐打。林德还有很多的控制技能，因此尽可能的为队友提升物理护甲。</span>
<br><span class="rvts21">另外，如果想让阿虎大人存活下来，就应该尽可能的保护他，为他提供治疗和护甲提升。因为最后一章的敌人大多都爱使用比较狠的源力技能。处于地理优势的黑环法师，会开头就下冰雹风暴或火流星。想要避免阿虎被杀，就应该尽可能的分散队伍，在吸取最后一个黑环士兵灵魂时，尽可能使用隐身，并把另外</span><span class="rvts20">3</span><span class="rvts21">个队友分散在上层，这样以来反倒使得敌人被动起来。</span>
<br><span class="rvts21">如果战斗结束后阿虎大人还活着，就可以得到一件额外的神圣装备（如果他死了，就没有这个奖励）。战斗结束后记得与阿虎大人谈话，无论是人是鬼，他都会告诉你接下来要做的事情就是去找玩具店老板，让老板帮你。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



